:root {
  --weight-bg: #ffffff;
  --weight-color: #383838;
  --averages-bg: #233445;
  --averages-color: #d8d8d8;
  --graph-bg: #a060a0;
  --graph-color: #202080;
}

* {
  box-sizing: border-box;
}

body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Helvetica", sans-serif;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

button {
  padding: 0;
  border: none;
  background-color: transparent;
  color: inherit;
  font: inherit;
  cursor: pointer;
  outline: none;
  white-space: nowrap;
}

.page {
  height: 100vh;
}

.weightInputSection {
  height: 4rem;
  background: #ffffff;
  background: var(--weight-bg);
  color: #383838;
  color: var(--weight-color);
}

.weightsSection {
  height: calc(50% - 4rem);
  padding: 8px 0;
}

.averagesSection {
  height: 15%;
  background: #c8c8ff;
}

.graphSection {
  height: 35%;
  background: #f8f480;
}

